import React from 'react';
import { FaQuoteLeft } from 'react-icons/fa';
import { QuoteConsumer } from '../contexts/quoteContext';

function Quote() {
  return (
    <QuoteConsumer>
      {({ randomColor, quoteObj: { quote, author }, quoteChanged }) => (
        <div className={quoteChanged ? 'quote fade' : 'quote'}>
          <div className="quote-box">
            <p className="quote-text" style={{ color: randomColor }}>
              <FaQuoteLeft style={{ color: randomColor }} />
              &nbsp;{quote}
            </p>
          </div>
          <div className="quote-author" style={{ color: randomColor }}>
            <span>- {author}</span>
          </div>
        </div>
      )}
    </QuoteConsumer>
  );
}

export default Quote;
